<h1 id="数据结构-data-structure" tabindex="-1"><a class="header-anchor" href="#数据结构-data-structure" aria-hidden="true">#</a> 数据结构 - Data Structure</h1>
<TOC/>
<h2 id="数据结构的构成" tabindex="-1"><a class="header-anchor" href="#数据结构的构成" aria-hidden="true">#</a> 数据结构的构成</h2>
<ul>
<li>逻辑结构 - 数据元素之间的逻辑关系</li>
<li>存储结构或物理结构 - 数据元素及其关系在计算机存储器中的存储方式</li>
<li>数据运算 - 数据元素的操作</li>
</ul>
<p><img src="https://image.hualihai.cn/blog/f959253ecb6748bc93fc5eb6bb060965" alt="组成"></p>
<h2 id="按照逻辑结构类型分类" tabindex="-1"><a class="header-anchor" href="#按照逻辑结构类型分类" aria-hidden="true">#</a> 按照逻辑结构类型分类</h2>
<h3 id="集合" tabindex="-1"><a class="header-anchor" href="#集合" aria-hidden="true">#</a> 集合</h3>
<p><strong>元素之间的关系</strong> : 无</p>
<p><strong>特点</strong> : 数据元素属于同一集合，别无其他逻辑关系。</p>
<h3 id="线性结构" tabindex="-1"><a class="header-anchor" href="#线性结构" aria-hidden="true">#</a> 线性结构</h3>
<p><strong>元素之间的关系</strong> : 一对一</p>
<p><strong>特点</strong> : 开始元素和终端元素都是唯一的，其余元素都有且仅有一个前驱元素和一个后继元素</p>
<p><strong>按操作分类</strong> :</p>
<ul>
<li>
<p><a href="/data-structure/linear-list/" target="_blank" rel="noopener noreferrer">线性表<OutboundLink/></a></p>
</li>
<li>
<p><a href="/data-structure/queue/" target="_blank" rel="noopener noreferrer">队列<OutboundLink/></a></p>
</li>
<li>
<p><a href="/data-structure/stack/" target="_blank" rel="noopener noreferrer">栈<OutboundLink/></a></p>
</li>
</ul>
<h3 id="树" tabindex="-1"><a class="header-anchor" href="#树" aria-hidden="true">#</a> 树</h3>
<p><strong>元素之间的关系</strong> : 一对多</p>
<p><strong>特点</strong> : 开始元素唯一，终端元素不唯一，除终端元素外，每个元素有一个或多个后继元素；除开始元素外，每个元素有且仅有一个前驱元素。</p>
<h3 id="图" tabindex="-1"><a class="header-anchor" href="#图" aria-hidden="true">#</a> 图</h3>
<p><strong>元素之间的关系</strong> : 多对多</p>
<p><strong>特点</strong> : 所有元素都可能有多个前驱元素和多个后继元素。</p>
